@import url("https://fonts.googleapis.com/css2?family=Inter:ital,opsz,wght@0,14..32,100..900;1,14..32,100..900&family=Leckerli+One&display=swap");
* {
  box-sizing: border-box;
  margin: 0;
  scroll-behavior: smooth;
  background-color: #11181f;
  font-family: "Inter", sans-serif;
  font-feature-settings: "clig" off, "liga" off;
  font-style: normal;
}

body {
  width: 100%;
  overflow-x: hidden;
}

.container {
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 40px;
}

section {
  margin-top: 100px;
}

h2 {
  color: #c2d3dc;
  font-feature-settings: "clig" off, "liga" off;
  font-size: 40px;
  font-weight: 600;
  line-height: 125%;
}
h2 span {
  color: #c70017;
  font-style: italic;
}

h3 {
  color: #c2d3dc;
  font-feature-settings: "clig" off, "liga" off;
  font-size: 24px;
  font-weight: 600;
  line-height: 125%;
}
h3 p {
  font-size: 20px;
}
h3 span {
  color: #c70017;
  font-style: italic;
}

.social {
  display: flex;
  width: 60px;
  height: 60px;
  justify-content: center;
  align-items: center;
  border-radius: 100px;
  border: 1px solid #adb8c1;
  transition: 0.5s all;
}
.social svg path {
  transition: 0.5s all;
}
.social:hover {
  background: #11181f;
}
.social:hover svg path {
  stroke: #403f3d;
}

.header {
  height: 60px;
  margin: 20px 0;
}
.header_logo {
  text-decoration: none;
  color: #c70017;
}
.header h1 {
  font-family: "Leckerli One", cursive;
  min-width: 130px;
  text-align: end;
  font-size: 42px;
  font-style: italic;
}
.header_wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
  min-height: 60px;
  align-items: center;
}
.header_logo-hamburger-wrapper {
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header_logo-hamburger-wrapper .hamburger {
  display: none;
}
.header_list {
  display: flex;
  list-style-type: none;
  padding: 0;
  column-gap: 40px;
}
.header_item {
  border-bottom: 2px solid transparent;
  transition: 0.5s all;
}
@media (hover: hover) {
  .header_item:hover {
    border-bottom: 2px solid #c70017;
  }
}
.header .menu-link_link {
  display: flex;
  align-items: center;
  gap: 8px;
  border-bottom: 2px solid transparent;
  transition: 0.5s all;
}
.header .menu-link_link:hover {
  border-bottom: 2px solid #c70017;
}

.link-text {
  color: #c2d3dc;
  font-feature-settings: "clig" off, "liga" off;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: 150%;
  text-decoration: none;
}

.enjoy {
  color: #c9d7e1;
  margin-top: 20px;
  padding: 100px;
  border-radius: 40px;
  overflow: hidden;
  position: relative;
  background: linear-gradient(rgba(0, 6, 12, 0.8) rgba(0, 15, 20, 0.8)), url(../assets/img/texture.jpg) center/cover no-repeat;
}
.enjoy * {
  background-color: transparent;
}
.enjoy_hero {
  position: relative;
  z-index: 2;
  display: flex;
  max-width: 530px;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  align-self: stretch;
  display: flex;
  gap: 40px;
  border-radius: 40px;
}
.enjoy_title {
  margin: 0;
  font-size: 72px;
  font-weight: 600;
  line-height: 105%;
}
.enjoy_title span {
  color: #c70017;
  font-style: italic;
}
.enjoy_descr {
  font-size: 16px;
  font-weight: 400;
  line-height: 150%;
}
.enjoy_button {
  position: relative;
  display: flex;
  gap: 8px;
  justify-content: center;
  height: 64px;
  width: 200px;
  border-radius: 100px;
  background: #11181f;
  text-align: center;
  padding: 20px 78px;
  transition: all 0.5s ease-out;
}
.enjoy_button:hover {
  color: #c70017;
}

.about_title {
  font-size: 59.9px;
}

.gallery {
  margin-top: 40px;
  display: flex;
  gap: 40px;
  justify-content: center;
}
.gallery_column {
  display: flex;
  flex-direction: column;
  gap: 40px;
}
.gallery_img {
  filter: brightness(90%) contrast(90%);
  max-width: 660px;
  overflow: hidden;
  border-radius: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.gallery_img img {
  transform: scale(1.1);
  width: 100%;
  transition: transform 0.5s ease;
}
.gallery_img:hover img {
  transform: scale(1);
  object-fit: cover;
}
.gallery_img__square {
  aspect-ratio: 1/1;
}
.gallery_img__rectangle {
  aspect-ratio: 1.5/1;
}

.modal {
  z-index: 99;
  background: rgba(6, 27, 39, 0.8);
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  color: #c2d3dc;
  font-family: "Inter", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.5;
}
.modal-closed {
  display: none;
}
.modal_window {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  margin: 0;
  width: 80%;
  border-radius: 40px;
  padding: 1px;
  background: #11181f;
}
.modal_wrapper {
  display: flex;
  border-radius: 40px;
  gap: 20px;
  background: transparent;
}
.modal_img {
  border-radius: 40px;
  width: 50%;
  object-fit: cover;
}
.modal_content {
  display: flex;
  gap: 20px;
  flex-direction: column;
  border-radius: 40px;
  background: transparent;
}
.modal_header {
  background: transparent;
}
.modal_title {
  background: transparent;
  font-weight: 600;
  font-size: 24px;
  line-height: 1.25;
}
.modal_descr {
  background: transparent;
  margin-top: 12px;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.5;
}
.modal_close {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  gap: 8px;
  border: 1px solid rgba(47, 52, 57, 0.5098039216);
  border-radius: 100px;
  padding: 10px;
  height: 44px;
  transition: all 0.5s ease;
  background: #c9d7e1;
}
.modal_close:hover {
  background: rgba(47, 52, 57, 0.5098039216);
  color: #c9d7e1;
  cursor: pointer;
}

.header_menu-link__underline {
  border-bottom: 1px solid #c2d3dc;
  cursor: default;
}

.projects {
  margin-top: 20px;
}
.projects_title {
  margin: 0 auto;
  max-width: 800px;
  text-align: center;
  color: #c2d3dc;
  font-size: 60px;
  font-weight: 600;
  line-height: 125%;
}
.projects_title span {
  color: #c70017;
  font-style: italic;
}

.tab {
  margin-top: 40px;
}
.tab_buttons {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 16px;
}
.tab_items {
  display: flex;
  justify-content: center;
}
.tab_content {
  margin-top: 40px;
  display: flex;
  flex-wrap: wrap;
  gap: 40px;
  justify-content: center;
}
.tab_refresh {
  display: flex;
  margin: 40px auto 0 auto;
  display: flex;
  width: 60px;
  height: 60px;
  justify-content: center;
  align-items: center;
  border-radius: 100px;
  border: 1px solid #455358;
  cursor: pointer;
}

.menu-card {
  width: 310px;
  height: 506px;
  border-radius: 40px;
  border: 1px solid #adb8c1;
  cursor: pointer;
  transition: all 0.5s;
}
.menu-card_img {
  width: 310px;
  height: 310px;
  flex-shrink: 0;
  overflow: hidden;
  border-radius: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.menu-card_img img {
  transform: scale(1.1);
  width: 100%;
  transition: all 0.5s;
}
.menu-card_img img:hover {
  transform: scale(1);
  object-fit: cover;
}
.menu-card_text {
  padding: 20px;
  display: flex;
  flex-direction: column;
  height: 196px;
  gap: 12px;
  color: #c2d3dc;
  background: transparent;
}
.menu-card_title {
  font-size: 24px;
  font-weight: 600;
  line-height: 125%;
}
.menu-card_descr {
  font-size: 16px;
  font-weight: 400;
  line-height: 150%;
}
.menu-card:hover img {
  transform: scale(1);
  object-fit: cover;
}

.tab-button {
  display: flex;
  padding: 16px 24px;
  justify-content: center;
  align-items: center;
  gap: 8px;
  border-radius: 100px;
  border: 1px solid #adb8c1;
  cursor: pointer;
  transition: all 0.5s;
}
.tab-button_img {
  width: 30px;
  height: 30px;
  display: flex;
  padding: 3px 7px;
  justify-content: center;
  align-items: center;
  border-radius: 40px;
}
.tab-button_img img {
  width: 16px;
  background: transparent;
  object-fit: cover;
}
.tab-button_descr {
  color: #c2d3dc;
  font-size: 16px;
  font-weight: 600;
  line-height: 150%;
  background: transparent;
}
.tab-button:hover {
  background: rgba(47, 52, 57, 0.5098039216);
}
.tab-button:hover .tab-button_img {
  background: #11181f;
}
.tab-button.active {
  background: rgba(47, 52, 57, 0.5098039216);
}
.tab-button.active .tab-button_img {
  background: #11181f;
}
.tab-button.active .tab-button_descr {
  color: #c9d7e1;
}

.footer {
  margin-top: 100px;
  margin-bottom: 40px;
}
.footer_wrapper {
  display: flex;
  padding: 100px;
  justify-content: center;
  align-items: center;
  gap: 100px;
  flex-wrap: wrap;
  border-radius: 40px;
  background: rgba(47, 52, 57, 0.5098039216);
}
.footer_column {
  max-width: 600px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  gap: 40px;
  flex: 1 0 0;
  background-color: transparent;
  width: 568px;
}
.footer_column * {
  background-color: transparent;
}
.footer_title {
  margin-top: 0;
  color: #c9d7e1;
}
.footer_title span {
  color: #c70017;
}
.footer_socials {
  display: flex;
  gap: 12px;
  transition: all 0.3s ease;
}
.footer_socials a {
  text-decoration: none;
}
.footer_socials a:hover i {
  color: #c70017;
}
.footer_socials i {
  text-decoration: none;
  color: #c9d7e1;
}
.footer_contact-us {
  color: #c9d7e1;
  font-size: 24px;
  font-weight: 600;
  line-height: 125%;
}
.footer_contacts {
  display: flex;
  flex-direction: column;
  gap: 16px;
  color: #c9d7e1;
  font-size: 16px;
  font-weight: 600;
  line-height: 150%;
}
.footer_contacts i {
  width: 50px;
}
.footer_contacts a {
  color: #c9d7e1;
  flex: 1;
  align-self: flex-start;
  text-decoration: none;
  border-bottom: 2px solid transparent;
  transition: 0.8s all;
}
.footer_contacts a span {
  border-bottom: 2px solid transparent;
  transition: 0.8s all;
}
@media (hover: hover) {
  .footer_contacts a span:hover {
    border-bottom: 2px solid #c70017;
  }
}

@media (max-width: 1439px) {
  .tab_content {
    max-width: 1000px;
  }
}
@media (max-width: 1199px) {
  .footer_wrapper {
    padding: 60px 40px;
  }
}
@media (max-width: 850px) {
  section {
    margin-top: 60px;
  }
  .modal_window {
    width: 688px;
  }
}
@media (max-width: 791px) {
  .header {
    position: relative;
  }
  .header .link-text {
    font-size: 32px;
    line-height: 125%;
  }
  .header_wrapper {
    display: block;
  }
  .header_nav {
    padding-top: 60px;
    z-index: 9;
    width: 100vw;
    height: 100vh;
    position: fixed;
    right: -100%;
    font-size: 32px;
    line-height: 125%;
    transition: 0.5s all;
  }
  .header_nav_open {
    top: 100px;
    right: 0;
    visibility: visible;
    opacity: 1;
  }
  .header_menu-link {
    z-index: 9;
    position: fixed;
    gap: 30px;
    right: -50%;
    top: 600px;
    transition: 0.5s right;
  }
  .header_menu-link .menu-link_link {
    gap: 16px;
  }
  .header_menu-link .menu-link_icon svg {
    width: 40px;
    height: 40px;
  }
  .header_menu-link_open {
    right: 50%;
    transform: translateX(50%);
    visibility: visible;
    opacity: 1;
  }
  .header_list {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 60px;
  }
  .header_logo-hamburger-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .header_logo-hamburger-wrapper .hamburger {
    display: flex;
    width: 44px;
    height: 44px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    gap: 8px;
    border-radius: 100px;
    border: 1px solid #455358;
  }
  .header_logo-hamburger-wrapper .hamburger span {
    display: block;
    width: 16px;
    height: 1.5px;
    background: #c2d3dc;
    transition: 0.5s all;
  }
  .header_logo-hamburger-wrapper .hamburger_open {
    z-index: 10;
  }
  .header_logo-hamburger-wrapper .hamburger_open span:nth-child(1) {
    transform: translateY(5px) rotate(-45deg);
  }
  .header_logo-hamburger-wrapper .hamburger_open span:nth-child(2) {
    transform: translateY(-5px) rotate(45deg);
  }
  .enjoy {
    padding: 100px 60px;
  }
  .gallery_img {
    max-width: 688px;
  }
  .gallery_img__rectangle {
    display: none;
  }
}
@media (max-width: 767px) {
  section {
    margin-top: 40px;
  }
  .container {
    max-width: 768px;
    padding: 0 16px;
  }
  h2 {
    font-size: 32px;
    line-height: 125%;
  }
  .enjoy {
    padding: 60px 16px;
  }
  .enjoy_title {
    font-size: 42px;
    line-height: 115%;
  }
  .about_title {
    font-size: 32px;
  }
  .gallery {
    flex-wrap: wrap;
  }
  .gallery_img img {
    height: 120%;
    object-fit: cover;
  }
  .footer_wrapper {
    padding: 40px 20px;
    gap: 20px;
  }
  .footer_column {
    min-width: 80%;
  }
  .menu_title {
    font-size: 32px;
  }
  .modal_window {
    max-width: 342px;
  }
  .modal_img {
    display: none;
  }
}
@media (max-width: 360px) {
  h2 {
    font-size: 24px;
  }
  .enjoy_title {
    font-size: 32px;
    text-align: center;
  }
  .enjoy_descr {
    display: none;
  }
  .enjoy_hero {
    align-items: center;
  }
  .modal_window {
    max-width: 220px;
  }
  .modal_descr, .modal_alert {
    display: none;
  }
  .about_title {
    font-size: 24px;
  }
  .footer_socials {
    margin: 20px auto;
  }
  .footer_column {
    gap: 0;
  }
  .footer_contact-us {
    display: none;
  }
  .footer_contacts {
    font-size: 12px;
  }
  .footer_contacts i {
    width: 30px;
  }
}